<template>
  <div class="main-container" v-if="product">
    <headers />
    <div class="container">
      <div class="row mt-5 full-detailes">
        <div class="col-4">
          <img
            src="https://picsum.photos/600/300/?image=25"
            alt=""
            class="w-100 ml-4"
          />
        </div>
        <div class="col-8">
          <h1>{{ product.title }}</h1>
          <h3>${{ product.price }}</h3>
          <input type="text" v-model.number="quantity" class="text-center col-1 mr-2 p-1" />
          <button class="btn btn-primary" @click="addtocart()">
            add to cart
          </button>
          <p class="mt-4">
            {{ product.description }}
          </p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {mapState,mapActions} from 'vuex'
import headers from "@/components/layouts/header.vue";
export default {
  components: {
    headers,
  },
  data(){
    return{
      quantity:1

    }
  },
  props: ["id"],
  computed: {
    ...mapState(['product'])
    // product() {
    //   return this.$store.state.product;
    // }
  },
    mounted() {
      // this.$store.dispatch("getproduct", this.id);
      this.getproduct(this.id);
    },
    methods: {
      ...mapActions(['addproducttocart','getproduct']),
      addtocart() {
        this.addproducttocart({
          product: this.product,
          quantity: this.quantity,
        });
      },
    },
  
};
</script>
<style scoped>
.full-detailes {
  border: 1px solid #333333;
}
</style>
