<template>
  <div class="col-xl-3 col-md-3 col-lg-3 col-sm-3 col-10 d-block m-auto">
    <b-card
      img-alt="Image"
      img-top
      tag="article"
      style="max-width: 20rem;"
      class="mb-2 mt-5"
    >
      <img class="w-100" :src="product.image" alt="" />
      <h4 class="card-title">
        <router-link :to="{ name: 'products', params: { id: product.id } }">{{
          product.title
        }}</router-link>
      </h4>
      <strong>${{ product.price }}</strong>
      <b-card-text>
        {{ product.description }}
      </b-card-text>

      <b-button href="#" variant="primary" @click="addtocart()"
        >Add to Cart</b-button
      >
    </b-card>
  </div>
</template>

<script>
import {mapActions} from 'vuex'
export default {
  props: ["product"],
  methods: {
    ...mapActions(["addproducttocart"]),
    addtocart() {
      this.addproducttocart({
        product: this.product,
        quantity: 1,
      });
    },
  },
};
</script>

