<template>
  <div
    class="dropdown-menu p-2"
    style="min-width:320px; right:0; left:auto"
    aria-labelledby="triggerId"
  >
    <div v-for="item in cart" :key="item.product.id">
      <div class="px-2 d-flex justify-content-between">
        <div>
          <strong>{{ item.product.title }}</strong>
          <br />{{ item.quantity }} x ${{ item.product.price }}
        </div>
        <div>
          <a href="#" class="badge badge-secondary" @click.prevent="removeproductfromcart(item.product)">remove</a>
        </div>
      </div>
      <hr />
    </div>
    <div class="d-flex justify-content-between">
      <span>Total:${{ carttotalprice }}</span>
      <a href="#" @click.prevent="clearcartitem()">Clear Cart</a>
    </div>
  </div>
</template>

<script>
import {mapState, mapGetters, mapActions} from 'vuex'
export default {
  computed: {
...mapState(["cart"]),
...mapGetters(["carttotalprice"])
    // cart() {
    //   return this.$store.state.cart;
    // },
    // carttotalprice() {
    //   return this.$store.getters.carttotalprice;
    // },
  },
  mounted() {
    this.getcartitems();
  },
  methods:{
    ...mapActions(['clearcartitem','getcartitems']),
  //or
    removeproductfromcart(product){
      this.$store.dispatch('removeproductfromcart', product);

    },
    // clearcartitem(){
    //   this.$store.dispatch('clearcartitem');
    // }
  }
};
</script>

