import Vue from 'vue'
import Router from 'vue-router'
import home from "@/components/pages/home.vue"
import products from "@/components/pages/products.vue"
Vue.use(Router)
export default new Router({
    mode:"history",
    routes:[
        {
            path:"/",
            name:'home',
            component:home
        },
        {
            path:"/products/:id",
            name:'products',
            component:products,
            props:true
        },
    ]

})